:root {
	--main-gradient: linear-gradient(to right, #fc5c7d, #6a82fb);
	--red: #f14c5d;
	--orange: #ff9e2f;
	--yellow: #ffd255;
	--blue: #14b1ee;
	--green: #fe8cfb;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	height: 100vh;
}

body {
	font-family: "Nunito", sans-serif;
	height: 100%;
}

button {
	outline: none;
	border: none;
	cursor: pointer;
}

a {
	text-decoration: none;
	color: #000;
}

ul {
	list-style-type: none;
}

h1 {
	font-size: 1.6rem;
}
